import qs from 'qs';
import axios from 'axios';

// const CLIENT_ID = "ba70e816b23488c";
const CLIENT_ID = "5d0b6e2c04b4bee";
const ROOT_URL = "https://api.imgur.com";


export default {
    login: function() {
        const queryParams = {
            client_id: CLIENT_ID,
            response_type: 'token'
        }

        window.location = `${ROOT_URL}/oauth2/authorize?${qs.stringify(queryParams)}`;
    },
    fetchImages: function(token) {
        return axios.get(`${ROOT_URL}/3/account/me/images`, {
            headers: {
                Authorization: `Bearer ${token}`,
            }
        });
    },
    uploadImages: function(token, images) {
        // ---- EXPLANATION ----
        // images is not actually an array but a FileList object which looks like an array with indexes
        // so to loop over the images object, we need to convert that into a proper array
        // looking at the API spec, the imgur API expects a base64 object or binary object or URL to the image when making the POST request
        // but the images filelist object just gives us the reference to the file but not the file itself
        // so we will use the JS built in FormData object to attach the actual file when making the POST request
        // the formData object contains the actual image file and the key of "image" used in the append method comes from the imgur API spec
        

        // map function returns an array of promises that correspond to each image upload
        const promises = Array.from(images).map(eachImage => {
            const formData = new FormData();
            formData.append('image', eachImage);

            return axios.post(`${ROOT_URL}/3/upload`, formData, {
                headers: {
                    Authorization: `Client-ID ${CLIENT_ID}`
                },
                Authorization: {
                    Token: `${token}`
                }
            })
        });

        // Promise.all() is a built in method that waits until all the promises are resolved and then it will resolve itself
        // this step ensures that we wait until every upload request is completed before we allow this function to continue
        return Promise.all(promises);
    }
}