<template>
    <div>
        <div v-if="isLoggedIn" class="images-container">
            <img width="500px" v-for="(image, index) in allImages" :key="index" :src="image.link" alt="imgur image">
            <!-- <img class="ui fluid image" src="https://i.imgur.com/uKGkYCc.jpg" alt="Akhil Eaga"> -->
        </div>
        <h2 v-else>
            Login to get started!
        </h2>
    </div>
</template>


<script>
import { mapActions, mapGetters } from "vuex";

export default {
    name: "ImageList",
    methods: {
        ...mapActions(['fetchImages'])
    },
    computed: {
        ...mapGetters(['allImages', 'isLoggedIn']),
    },
    created: function() {
        this.fetchImages();
    }
}
</script>

