import router from '../../router';
import api from '../../api/imgur';


const imageModule = {
    state: function() {
        return {
            images: [],
        };
    },
    getters: {
        allImages: function(state) {
            return state.images;
        }
    },
    mutations: {
        setImages: function(state, images) {
            state.images = images;
        }
    },
    actions: {
        fetchImages: async function({ rootState, commit }) {
            // rootState.auth.token - auth is used because in the index.js, in modules object, "auth" key is used to refer to the "authModule"
            const token = rootState.auth.token;
            const response = await api.fetchImages(token);
            commit('setImages', response.data.data);
        },
        uploadImages: async function({ rootState }, images) {
            const token = rootState.auth.token;
            // call the api to uplaod images
            await api.uploadImages(token, images);
            // redirect to imagelist page
            router.push('/');
        }
    }
}

export default imageModule;