import { createStore } from 'vuex';
import authModule from './modules/auth';
import imageModule from './modules/images';


// Use the following resource to understand how to hook up the Vuex with your app
// https://vuex.vuejs.org/guide/#the-simplest-store

const store = createStore({
    modules: {
        auth: authModule,
        images: imageModule,
    }
});

export default store;